.space-controller{
    flex:1;
    display: flex;
    flex-direction: column;
    overflow: auto;
    align-items: center;
    .advertisingWrap{
        position: absolute;
        bottom:0;
        left:0;
        z-index:11;
        width: 100%;
        background:rgba(0,0,0,0.70);
        height:80px;
        &>div{
            cursor: pointer;
            width: 100%;
            height: 100%;
            text-decoration: none;
            display: flex;
            flex-direction: row;
            align-items: center;
            img{
                border:none;
            }
            div{
                padding:20px;
                span{
                    flex:1;
                    font-size:14px;
                    color:#fff;
                    overflow: hidden;
                    text-overflow:ellipsis;
                    display: -webkit-box;
                    -webkit-line-clamp: 2;
                }
            }
        }

        .closed{
            cursor: pointer;
            position: absolute;
            right:8px;
            top:12px;
            width: 16px;
            height: 16px;
            opacity:.7;
            background-image: url('../../assets/images/new/icon-close2.svg');
        }
    }
    .accountsWrap{
        transition:all .2s ease-out;
        transform: translateX(-100%);
        width: 100%;
        height: 100%;
        position: absolute;
        z-index: 10;
        left:0;
        top:0;
        background-color: rgba(0,0,0,.5);
        display: flex;
        flex-direction: row;
        &.show{
            transform: translateX(0);
        }
        .accounts{
            width: 330px;
            height: 100%;
            padding: 0 14px;
            background:#ffffff;
            display: flex;
            flex-direction: column;
            padding-top: 20px;
            padding-bottom: 10px;
            .row0 {
                font-size:16px;
                font-family:Arial-BoldMT;
                font-weight:normal;
                color:rgba(23,22,66,1);
                line-height:18px;
                margin-bottom: 10px;
            }
            .buttonGroup{
                bottom:14px;
                display: flex;
                flex-direction: row;
                justify-content: space-between;
                button{
                    cursor: pointer;
                    height:34px;
                    width: 140px;
                    color: #fff;
                    font-size: 16px;
                    border-radius: 4px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    border:none;
                    margin-right: 3px;
                    &:before{
                        width: 16px;
                        height: 16px;
                        content:'';
                        margin-right: 10px;
                    }
                    &.receive{
                        background:rgba(66,132,255,1);
                        box-shadow:0px 2px 12px 0px rgba(15,28,114,0.08);
                        &:before{
                            background-image: url('../../assets/images/cool/icon-create.svg');
                        }
                    }
                    &.send{
                        background:rgba(66,132,255,1);
                        box-shadow:0px 2px 12px 0px rgba(15,28,114,0.08);
                        &:before{
                            background-image: url('../../assets/images/cool/icon-in.svg');
                        }
                    }
                }
    
            }
            .row1{
                height: 35px;
                background:rgba(247,248,250,0.90);
                display: flex;
                flex-direction: row;
                align-items: center;
                margin-top: 10px;
                
                .cell{
                    flex: 1;
                    display: flex;
                    flex-direction: row;
                    justify-content: center;
                    align-items: center;
                    height: 16px;
                    cursor: pointer;
                    font-size: 14px;
                    &:before{
                        content: '';
                        width: 12px;
                        height: 12px;
                        background-repeat: no-repeat;
                        margin-right: 6px;
                    }
                    &:first-child{
                        &:before{
                            background-image: url('../../assets/images/new/icon-create.svg');
                        }
                    }
                    &:last-child{
                        &:before{
                            background-image: url('../../assets/images/new/icon-import.svg');
                        }
                    }
                }
            }
            .row2{
                display: flex;
                padding:8px 0;
                .line {
                    border-right: 2px solid #999;
                    width: 30px;
                    margin-right: 30px;
                }
                .cell{
                    display: flex;
                    flex-direction: row;
                    align-items: center;
                    font-size: 15px;
                    font-weight: bold;
                    margin-left: 3px;

                    & > span:first-child{
                        color:#505160;
                    }
                    & > span:last-child{
                        margin-left: 12px;
                        color:#888998;
                    }
                }
            }
            .row3{
                flex:1;
                overflow:auto;
                margin: 0 -1px 0;
                &::-webkit-scrollbar {
                    width: 4px;
                    height: 4px;
                }
                &::-webkit-scrollbar-thumb {
                    border-radius: 4px;
                    background: #C2C8D5;
                    position:relative;
                    left:-5px;
                }
                &::-webkit-scrollbar-track {
                    background: transparent;
                }
                .cell{
                    cursor: pointer;
                    margin-bottom: 8px;
                    margin-left: 1px;
                    border-radius:4px;
                    height:98px;
                    display: flex;
                    flex-direction: column;
                    justify-content: space-between;
                    transition:all .2s ease-in-out;
                    &:hover{
                        transform:translate(-1px,-1px);
                    }
                    &.selected{
                        .top{
                            .name{
                                &:after{
                                    content: '';
                                    width: 18px;
                                    height: 18px;
                                    background-size:100%;
                                    background-image: url("../../assets/images/cool/icon-selected-account.svg");
                                }
                            }
                        }
                    }

                    &.cell1{
                        background-image:linear-gradient(-137deg, #3699FF 0%, #4284FF 100%);
                    }
                    &.cell2{
                        background-image:linear-gradient(-137deg, #7351F4 0%, #6742FF 100%);
                    }
                    &.cell3{
                        background-image:linear-gradient(-137deg, #BD57DC 0%, #9445C7 100%);
                    }
                    &.cell4{
                        background-image:linear-gradient(-137deg, #ED984C 0%, #E07E3F 100%);
                    }
                    &.cell5{
                        background-image:linear-gradient(-137deg, #55B63B 0%, #539F35 100%);
                    }
                    .top{
                        flex:1;
                        padding:12px 12px 8px;
                        display: flex;
                        flex-direction: column;
                        .name{
                            display: flex;
                            flex-direction: row;
                            justify-content: space-between;
                            font-size:16px;
                            font-weight: bold;
                            color:#fff;
                            height: 24px;
                            line-height: 24px;
                        }
                        .asset{
                            height: 24px;
                            color: #ebebeb;
                            display: flex;
                            font-size: 14px;
                            flex-direction: row;
                            align-items:flex-end;
                            & > span{
                                &:last-child{
                                    margin-left: 10px;
                                }
                            }
                        }
                    }
                    .bottom{
                        background:rgba(255,255,255,0.28);
                        height:29px;
                        border-radius:0 0 4px 4px;
                        padding:0 12px;
                        display: flex;
                        flex-direction: row;
                        justify-content: space-between;
                        align-items:center;
                        font-size:12px;
                        color: #fff;
                        user-select: text;
                        &>div{
                            margin-left: 9px;
                            width: 24px;
                            height: 29px;
                            display: flex;
                            background-image: url('../../assets/images/cool/icon-copy.svg');
                            .copy{
                                width: 100%;
                                height: 100%;
                            }
                        }
                    }
                }
            }
        }
        .closed{
            cursor: pointer;
            width: 40px;
            height: 100%;
            background-image: url('../../assets/images/new/icon-close.svg');
            background-repeat: no-repeat;
            background-position: center 12px
        }
    }
}
.accountsPage {
    flex: 1;
    flex-direction: column;
    overflow: hidden;
    display: flex;
    background-color: #F7F8FA;
