            background: #FFFFFF;
            border-radius: 4px;
            padding:14px;
            display: flex;
            flex-direction: column;
            border-radius: 6px;
            .title{
                font-size: 20px;
                color: #0E0E2D;
                height: 28px;
                line-height: 28px;
            }
            .option{
                margin-top: 20px;
                display: flex;
                flex-direction: column;
                text-align: center;
                & > span{
                    height: 20px;
                    font-size: 14px;
                    color: #505160;
                    line-height: 20px;
                    margin-bottom: 8px;
                }
                .block{
                    width: 100%;
                    padding:10px;
                    background: #F7F8FA;
                    border-radius: 2px;
                    font-size: 12px;
                    color: #0E0E2D;
                    word-break:break-all;
                    user-select:text;
                    display: flex;
                    flex-direction:row;
                    flex-wrap: wrap;
                    text-align: left;
                    .cell{
                        width: 23%;
                        margin-left: 2%;
