                        line-height: 28px;
                        text-align: center;
                        background-color: #fff;
                        border-radius:4px;
                        font-size:11px;
                        color:rgba(112,118,143,1);
                        border:1px solid #e0e1e2;
                        &:nth-child(1), &:nth-child(2), &:nth-child(3), &:nth-child(4){
                            margin-top: 0;
                        }
                        &:nth-child(4n-3){
                            margin-left: 0;
                        }
                    }
                }
            }
        }
    }
