                            width: 15px;
                            height: 15px;
                            background-image: url('../../assets/images/new/market_logo.svg');
                        }
                        &:after {
                            content: "";
                            width: 4px;
                            height: 6px;
                            background-image: url('../../assets/images/new/icon-enter2.svg');
                            background-size:100%;
                        }
                    }
                    .ieo_ing{
                        display: flex;
                        flex-direction: row;
                        height: 17px;
                        align-items: center;
                        font-size: 13px;
                        &:before{
                            content:'';
                            width: 12px;
                            height: 15px;
                            background-image: url('../../assets/images/new/icon-hot.svg');
                            margin-right: 5px;
                        }
                    }
                }
            }
        }
        .tokens{
            .tokenItem{
                border: 1px solid #f1f1f1;
                background-color: #fff;
                border-radius: 4px;
                height: 60px;
                display: flex;
                flex-direction: row;
                align-items: center;
                padding:0 14px;
                cursor: pointer;
                margin-bottom: 10px;
                transition: all .2s ease-in-out;
                &:hover{
                    transform: translate(-1px,-1px);
                    background-color: rgba(255,255,255,.8);
                }
                &:first-child{
                    margin-top: 0;
                    .name{
                        flex:2;
                    }
                }
                img {
                    width: 32px;
                }
                .name {
                    font-size: 14px;
                    color: #505160;
                    margin-left: 10px;
                    display: flex;
                    flex-direction: column;
                    align-items: flex-start;
                    span{
                        &:first-child{
                            font-size:16px;
                            font-family:Arial-BoldMT;
                            font-weight:normal;
                            color:rgba(23,22,66,1);
                            line-height:18px;

                        }
                        &:last-child{
                            font-size: 12px;
                            color: #888998;
                            font-family:ArialMT;
                            line-height:14px;
                        }
                    }
                    .income {
                        margin-top: 2px;
                        display: flex;
                        flex-direction: row;
                        align-items: center;
                        font-size: 12px;
                        color: #E2AB58;
                        &:before {
                            content: '';
                            width: 8px;
                            height: 9px;
                            background-image: url('../../assets/images/new/usdt/icon-income.svg');
                            margin-right:3px;
                        }
                    }
                }
                .worth{
                    flex:1;
                    display: flex;
                    flex-direction: column;
                    align-items: flex-end;
                    span{
                        &:first-child{
                            font-size:16px;
                            font-family:Arial-BoldMT;
                            font-weight:normal;
                            color:rgba(23,22,66,1);
                            line-height:18px;
                        }
                        &:last-child{
                            font-size: 12px;
                            color: #888998;
                            font-family:ArialMT;
                            line-height:14px;
                        }
                    }
                }
            }
        }
        &>div:last-child{flex:1}
    }
}
