.greyModal .walletOption {
    background: #FFFFFF;
    border-radius: 4px;
    padding: 15px;
    transition: all 200ms ease;
    cursor: pointer;
    position: relative;
    margin-bottom: 32px;
    &:hover {
        box-shadow: 0px 1px 12px 0px rgba(0, 0, 0, 0.1);
    }
}
.tip {
    height: 32px;
}