.privateKeyImport {

    .privateKeyInput {
        background: #F7F8FA;
        padding: 14px;
        font-size: 15px;
        line-height: 20px;
        border-radius: 2px;
        user-select: all;
        word-break: break-word;
        border: none;
        width: 100%;
        box-sizing: border-box;
        resize: none;
    }
    .step {
        padding: 0 20px;
        padding-bottom: 20px;
    }
    .stepText {
        height: 16px;
        font-size: 14px;
        font-family: ArialMT;
        color: rgba(23,22,66,1);
        line-height: 16px;
        text-align: center;
        margin-bottom: 20px;
    }
    .logoName {
        height: 18px;
        font-size: 16px;
        font-family: Arial-BoldMT;
        font-weight: normal;
        color: rgba(23,22,66,1);
        line-height: 18px;
        margin-top: 20px;
    }
    .BTN {
        width: 180px;
        margin: 0 auto;
    }
}
