.main {
    height: 20px;
    line-height: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 16px;
    text-align: center;
    .num {
        width: 20px;
        border-radius: 20px;
        background: #F3F4FA;
        color: #A4ADBD;
        font-size: 16px;
        &.active {
            color: #F3F4FA;
            background:linear-gradient(180deg,rgba(86,108,251,1) 0%,rgba(86,108,251,1) 45%,rgba(53,76,230,1) 100%);
        }
    }
    .line {
        height: 1px;
        width: 40%;
        background-color: #F3F4FA;
        &.lineActive {
           background-color: #566CFB;
           height: 1px;
           width: 40%;
        }
    }
}
