.writingPhrase {
    .step {
        padding-right: 20px;
        padding-left: 20px;
        padding-bottom: 20px;
    }
    .stepText {
        height: 16px;
        font-size: 14px;
        font-family: ArialMT;
        color: rgba(23,22,66,1);
        line-height: 16px;
        text-align: center;
        margin-bottom: 20px;
    }
    .BTN {
        width: 180px;
        margin: 0 auto;
    }
}
.wordList {
    margin-bottom: 30px;
    background: #F3F4FA;
    border-radius: 4px;
    padding: 15px;
    text-align: center;
    font-size: 14px;
    line-height: 25px;
    display: flex;
    flex-wrap: wrap;
    user-select: all;
    justify-content:flex-start;
    height: 190px;
    .word {
        height: 29px;
        color: #70768f;
        border: 1px solid #e1e1e1;
        border-radius: 4px;
        width: 30%;
        background-color: #fff;
        padding:2px 0;
        margin:0 5% 8px 0;
        &:nth-child(3n){
            margin-right:0;
        }
        &:nth-child(10),&:nth-child(11),&:nth-child(12){
            margin-bottom: 0;
        }
    }
}
